.Title {
  display: flex;
  justify-content: space-between;
  align-items: center;
  color: #fff;
  font-family: Poppins;
  font-size: 60px;
  line-height: 60px;
  font-weight: 800;
  @media screen and (max-width: 575px) {
    flex-direction: column;
    font-size: 40px;
    line-height: 70px;
  }
  &:before {
    content: "";
    height: 3px;
    margin-right: 20px;
    width: 100px;
    display: block;
    background-color: #eb83f8;
    @media screen and (max-width: 575px) {
      margin: 0;
    }
  }

  &:after {
    content: "";
    margin-left: 20px;
    height: 3px;
    width: 100px;
    display: block;
    background-color: #eb83f8;
    @media screen and (max-width: 575px) {
      margin: 0;
    }
  }
}
.Title.TitleSmall {
  font-size: 40px;
  margin-bottom: 40px;
   @media screen and(max-width: 991px) {
     font-size: 35px;
   }
      @media screen and(max-width: 767px) {
     font-size: 30px;
   }
   @media screen and(max-width: 767px){
     font-size: 20px;
   }
}
